/* Enhancing video container */
.video-container {
    position: relative;
    overflow: hidden;
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.7) 100%);
    /* This adds a slight dark gradient to your video for better text visibility */
}

.background-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1; 
}

/* Overlay content styling */
.content-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

h1 {
    font-size: 3rem;
    margin-bottom: 2rem;
    color: white;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
}

.btn {
  display: inline-block;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  background-color: #3498db;  /* Initial vibrant blue color */
  color: white;
  border: none;
  border-radius: 50px;  /* Giving it rounded corners for modern feel */
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2); /* Soft shadow */
  cursor: pointer;
  outline: none;
  position: relative;  /* For the pseudo-element hover effect */
  overflow: hidden;  /* For the pseudo-element hover effect */
}

.btn:hover {
  background-color: #29b933;  /* A bit darker shade on hover */
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  scale: 1.2;
}

/* Pseudo-element for a fancy hover effect */
.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  transform: skewX(-45deg) translateX(-150%); /* Initially positioned out of the button */
  transition: all 0.4s ease-in-out;
}

.btn:hover::before {
  transform: skewX(-45deg) translateX(150%); /* Slide in on hover */
}
