/* ... Other styles ... */

body {
  background: #141414; /* Dark background like Netflix */
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  overflow: hidden;
}

.animation-container {
  font-family: 'Montserrat', sans-serif;
  display: inline-block;
  position: relative;
}
@keyframes heartbeat {
  0%, 100% {
      transform: scale(1);
  }
  50% {
      transform: scale(1.05);
  }
}

/* Shine effect */
@keyframes shine {
  0%, 100% {
      background-position: -200% 0;
  }
  50% {
      background-position: 200% 0;
  }
}

.letter {
  /* ... Existing styles ... */
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.8), transparent);
  background-size: 200% 100%;
  background-repeat: no-repeat;
  background-position: -200% 0;
  animation: slide 1s forwards, smokeEffect 1s forwards, heartbeat 1.5s 2s infinite, shine 1.5s 1.5s forwards;
}

/* ... Rest of the styles ... */


.letter {
  position: relative;
  display: inline-block;
  font-size: 3rem;
  color: transparent; /* Initially, the letter is transparent */
  text-shadow: 0 0 5px rgb(255, 255, 255); /* Smokey effect */
  animation: slide 1s forwards, smokeEffect 1s forwards;
}

.letter::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: -100%;
  color: #c7060faa; /* Netflix Red */
  white-space: nowrap;
  animation: overlay-slide 1s forwards;
}

/* ... Previous styles ... */

.blast-out {
  animation: blastAnimation 1s forwards;
}

@keyframes blastAnimation {
  to {
      transform: scale(6); 
      opacity: 0;  
  }
}

@keyframes slide {
  to {
      transform: translateX(-50%);
      color: white; /* Make letter visible after slide */
  }
}

@keyframes overlay-slide {
  to {
      transform: translateX(100%);
  }
}

@keyframes smokeEffect {
  0% {
      text-shadow: 0 0 5px white;
  }
  50% {
      text-shadow: 0 0 15px white, 0 0 20px white, 0 0 25px white;
  }
  100% {
      text-shadow: 0 0 5px rgba(255, 255, 0, 0.575);
  }
}

@keyframes fadeIn {
  to {
      opacity: 1;
  }
}
